<script>
	import color from '../../uni_modules/uview-ui/libs/config/color';
	export default {
		data() {
			return {
				loginShow: true,
				rightsShow: false,
				remainShow: false,
				xieyiSelect: false,
				rightIndex: 3,
				timeLeft: 3600, // 单位：秒（1小时=3600秒）
				timer: null
			}
		},
		computed: {
			// 将秒数转换为时分秒格式
			formattedTime() {
				const hours = Math.floor(this.timeLeft / 3600)
				const minutes = Math.floor((this.timeLeft % 3600) / 60)
				const seconds = this.timeLeft % 60

				// 补零函数
				const pad = n => n.toString().padStart(2, '0')

				return `限时优惠 ${pad(hours)}:${pad(minutes)}:${pad(seconds)}`
			}
		},

		created() {
			const code = this.$route.query.code
			const state = this.$route.query.state

			if (code) {
				this.exchangeCode(code)
			} else {
				console.error('微信授权失败:', this.$route.query)
			}
		},

		mounted() {
			this.startTimer()
		},

		beforeDestroy() {
			this.clearTimer()
		},

		methods: {
			selectClick() {
				this.xieyiSelect = !this.xieyiSelect
			},
			rightClick(index) {
				this.rightIndex = index
			},
			startTimer() {
				this.timer = setInterval(() => {
					if (this.timeLeft > 0) {
						this.timeLeft--
					} else {
						this.clearTimer()
						this.$emit('time-end') // 触发结束事件
					}
				}, 1000)
			},

			clearTimer() {
				if (this.timer) {
					clearInterval(this.timer)
					this.timer = null
				}
			},

			loginClick() {
				const appId = 'wx92ce0621f2f9880b'
				const redirectUri = encodeURIComponent(
					'http://localhost:1024/pages/shouye/message')
				const state = 'STATE' // 建议随机生成防CSRF
				const url =
					`https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appId}&redirect_uri=${redirectUri}&response_type=code&scope=snsapi_userinfo&state=${state}#wechat_redirect`
				window.location.href = url
			},
			async exchangeCode(code) {
				try {
					const res = await axios.post('/api/wechat/login', {
						code
					})
					// 存储用户信息
					localStorage.setItem('user', JSON.stringify(res.data))
					this.$router.push('/')
				} catch (error) {
					console.error('登录失败:', error)
				}
			}
		}
	}
</script>

